import { Component } from "react";
import FirstPageIcon from '@material-ui/icons/FirstPage';
import LastPageIcon from '@material-ui/icons/LastPage';
import NavigateNextIcon from '@material-ui/icons/NavigateNext';
import NavigateBeforeOutlinedIcon from '@material-ui/icons/NavigateBeforeOutlined';
import GetAppIcon from '@material-ui/icons/GetApp';
//import * as CryptoJS from "crypto-js"
var CryptoJS = require("crypto-js");

class StructBox extends Component<{ data: any, handler: any, state: any, download: any }, { encrypt: string, objectNumber: number }>{

    constructor(props: any) {
        super(props)
        this.prewObj = this.prewObj.bind(this)
        this.nextObj = this.nextObj.bind(this)
        this.state = {
            encrypt: "",
            objectNumber: 0
        }
    }

    checkRemove(json: any) {
        this.props.state.keyRemove.map((k: any) => {
            json.map((val: any) => {
                delete val[k]
            })
        })
    }

    crypt(salt: any, text: any) {
        const textToChars = (text: string) => text.split("").map((c) => c.charCodeAt(0));
        const byteHex = (n: any) => ("0" + Number(n).toString(16)).substr(-2);
        const applySaltToChar = (code: any) => textToChars(salt).reduce((a, b) => a ^ b, code);

        return text
            .toString()
            .split("")
            .map(textToChars)
            .map(applySaltToChar)
            .map(byteHex)
            .join("");
    };

    decrypt(salt: string, encoded: any) {
        const textToChars = (text: string) => text.split("").map((c) => c.charCodeAt(0));
        const applySaltToChar = (code: any) => textToChars(salt).reduce((a, b) => a ^ b, code);
        return encoded
            .match(/.{1,2}/g)
            .map((hex: any) => parseInt(hex, 16))
            .map(applySaltToChar)
            .map((charCode: any) => String.fromCharCode(charCode))
            .join("");
    };


    encrypt(json: any) {
        this.props.state.keyGen.map((key: any) => {
            json.map((value: any) => {
                //is Array
                if (Array.isArray(value[key])) {
                    let m_array: any = []
                    value[key].map((element: any, i: number) => {
                        m_array.push(
                            this.props.state.encrypt_decrypt === "encrypt"
                                ? this.crypt("!$JSON-ANON$!", element)
                                : this.decrypt("!$JSON-ANON$!", element)
                        );
                    });
                    value[key] = m_array
                } else {
                    // is string
                    value[key] = this.props.state.encrypt_decrypt === "encrypt"
                        ? this.crypt("!$JSON-ANON$!", value[key])
                        : this.decrypt("!$JSON-ANON$!", value[key]);
                }
            })
        })
    }

    prewObj(e: any, min: number, type: string) {
        if (this.state.objectNumber > min) {
            if (type == "back") {
                this.setState({
                    objectNumber: this.state.objectNumber - 1
                })
            } else {
                this.setState({
                    objectNumber: min
                })
            }
        }
    }

    nextObj(e: any, max: number, type: string) {
        if (this.state.objectNumber < max) {
            if (type == "forward") {
                this.setState({
                    objectNumber: this.state.objectNumber + 1
                })
            } else {
                this.setState({
                    objectNumber: max
                })
            }
        }
    }

    render() {
        const json = JSON.parse(this.props.data)
        let max_obj = json ? json.length : 0
        if(json && this.state.objectNumber === 0){ 
            this.setState({
                objectNumber: 1
            })
        }
    
        this.checkRemove(json)
        this.encrypt(json)
        return (
            <div>
                <div className="obj-control-box">
                    <p className="obj-control" onClick={(e) => this.prewObj(e, 0, "first")}><FirstPageIcon /></p>
                    <p className="obj-control" onClick={(e) => this.prewObj(e, 0, "back")}><NavigateBeforeOutlinedIcon /></p>
                    <p className="obj-control" >{this.state.objectNumber + "/" + max_obj}</p>
                    <p className="obj-control" onClick={(e) => this.nextObj(e, json.length, "forward")}><NavigateNextIcon /></p>
                    <p className="obj-control" onClick={(e) => this.nextObj(e, json.length, "last")}><LastPageIcon /></p>
                </div>
                <div><pre style={{ fontSize: "1em" }}>{json ? JSON.stringify(json[this.state.objectNumber-1], null, 2) : "No JSON structure in this moment, upload it! :)"}</pre></div>
                <div className="download-box">
                    <div className="encrypt-btn" onClick={(e) => this.props.download(e, json)}>Download Data  <GetAppIcon/></div>
                </div>
            </div>
        )
    }
}

export default StructBox