import { Checkbox, FormControlLabel, FormGroup } from "@material-ui/core";
import DeleteOutlineOutlinedIcon from '@material-ui/icons/DeleteOutlineOutlined';
import DeleteIcon from '@material-ui/icons/Delete';
import CheckBoxOutlineBlankIcon from '@material-ui/icons/CheckBoxOutlineBlank';
import { Component } from "react";

class FieldBox extends Component<{ data: any, handler: any, state: any }, { keyGen: any, keyRemove: any }>{

    constructor(props: any) {
        super(props)
        this.state = {
            keyGen: [],
            keyRemove: []
        }
    }

    render() {
        const json = JSON.parse(this.props.data)
        return (
            <div>
                <div className="legend-box">
                    <p className="legend"><DeleteOutlineOutlinedIcon />: Remove field in Anon file</p>
                    <p className="legend"><CheckBoxOutlineBlankIcon />: Encrypt field in Anon file</p>
                </div>
                <FormGroup>
                    {
                        json ? Object.keys(json[0]).map((el: any) =>
                            <div>
                                <Checkbox
                                    className="delete-icon"
                                    icon={<DeleteOutlineOutlinedIcon />}
                                    checkedIcon={<DeleteIcon />}
                                    onClick={(e) => this.props.handler(e, el, "del")}
                                />
                                <FormControlLabel control={<Checkbox className="encrypt-icon" value={el} onClick={(e) => this.props.handler(e, el, "gen")} />} label={el} />
                            </div>
                        )
                            : null
                    }

                </FormGroup>
            </div>)
    }
}

export default FieldBox