import { upload } from "@testing-library/user-event/dist/upload";
import { Component } from "react";
import FieldBox from "./Field.component";
import StructBox from "./Struct.component";
import PublishIcon from '@material-ui/icons/Publish';
import loading from "../img/loading.gif"
import background from "../img/background.png";
import logo from "../img/logo.png"
import * as fs from 'fs';

class Upload extends Component<{ file: string }, { fileName: string, uploadJSON: any, keyGen: any, keyRemove: any, loading: boolean, first_choice: boolean, encrypt_decrypt: string  }> {

    constructor(props: any) {
        super(props);
        this.onChangeFile = this.onChangeFile.bind(this)
        this.handler = this.handler.bind(this)
        this.download = this.download.bind(this)
        this.choice = this.choice.bind(this)
        this.state = {
            fileName: '',
            uploadJSON: null,
            keyGen: [],
            keyRemove: [],
            loading: false,
            first_choice: true,
            encrypt_decrypt: ""

        };

    }
    handler(e: any, value: string, type: string) {
        if (e.target.checked) {
            //append to array
            if (type == "gen") {
                this.setState({
                    keyGen: this.state.keyGen.concat([value]),
                })
            } else {
                this.setState({
                    keyRemove: this.state.keyRemove.concat([value])
                })
            }
        } else {
            //remove from array
            if (type == "gen") {
                this.setState({
                    keyGen: this.state.keyGen.filter(function (val: any) { return val !== value })
                })
            } else {
                this.setState({
                    keyRemove: this.state.keyRemove.filter(function (val: any) { return val !== value })
                })
            }
        }
    }

    loading(state: boolean) {
        this.setState({
            loading: !state
        })
    }

    download(e: any, json: any) {
        let json_to_txt = JSON.stringify(json);
        //download encrypt
        const jsonString = `data:text/json;chatset=utf-8,${encodeURIComponent(
            json_to_txt
        )}`;
        const link = document.createElement("a");
        link.href = jsonString;
        link.download = "encrypted.json";
        link.click();
    }



    onChangeFile = async (e: any) => {
        const fileReader = new FileReader();
        const fileJSON = e.target.files[0];
        fileReader.readAsText(fileJSON, "UTF-8");
        fileReader.onload = e => {
            this.setState({
                fileName: fileJSON.name,
                uploadJSON: e.target?.result,
            })
        };
    };

    choice(e: any, type: string){
        this.setState({
            first_choice: false,
            encrypt_decrypt: type
        })
    }

    render() {
        return (
            <div>
                {this.state.loading ?
                    <div className="loading">
                        <h2 className="crash">If it crash is not my fault!</h2>
                        <img src={loading}></img>
                    </div> : null
                }
                {
                    this.state.first_choice ?
                    <div className="first-choice-box" style={{backgroundImage: `url(${background})`}}>
                        <div className="choise-box">
                            <img className="logo" src={logo} />
                            <h3>Choose what to do with your data:</h3>
                            <div className="choice-option-box">
                                <p className="choice-option" onClick={(e) => this.choice(e, "encrypt")}>Encrypt Data from JSON</p>
                                <p className="choice-option" onClick={(e) => this.choice(e, "decrypt")}>Decrypt Data from JSON</p>
                            </div>
                        </div>
                    </div> : null
                }
                <div className="json-box" style={{ margin: "50px" }}>
                    <h1>Upload a JSON file to <span style={{ textTransform: "capitalize", padding: "10px", background:"#eee", border: "1px dashed #ccc" }}>{this.state.encrypt_decrypt}</span></h1>
                    <p className="description">This version (0.0.1 beta) only supports JSON formats with top level hierarchy (does not support nested objects yet).</p>
                    <div className="upload-filed">
                        {
                            this.state.fileName ?
                                <p className="file-name">You read the data from the file: <span className="file-name-evidence">{this.state.fileName}</span></p>
                                : null
                        }
                        <label
                            htmlFor="filePicker"
                            style={{ borderRadius: "10px", background: "#f6be61", padding: "15px 20px", color: "#333", fontWeight: "700", position: "relative", top: "30px" }}>
                            Upload <PublishIcon className="upload-icon" />
                        </label>
                        <input
                            id="filePicker"
                            style={{ visibility: "hidden" }}
                            type={"file"}
                            onChange={this.onChangeFile}
                        >
                        </input>
                    </div>
                    <div className="json-field-box json-uploaded-box">
                        <p className="explain">Show all the fields of a single Tweet</p>
                        <div className="field-box">
                            <FieldBox data={this.state.uploadJSON} handler={this.handler} state={this.state} />
                        </div>
                    </div>
                    <div className="json-structure-box json-uploaded-box">
                        <p className="explain">Show the structure of a single Tweet</p>
                        <div className="struct-box">
                            <StructBox data={this.state.uploadJSON} handler={this.handler} state={this.state} download={this.download} />
                        </div>
                    </div>
                </div>
            </div>
        )
    }

}
export default Upload